.skill-preview {
  gap: 23px;
  @include for-mobile-layout {
    flex-direction: column;
    align-items: center;
    width: 327px;
  }
  .skill-img-container {
    width: 72px;
  }
  .skill-info {
    width: 445px;
    
    h4 {
      color: $clr3;
      font-family: livvic-bold;
      margin-bottom: 1rem;
      @include for-mobile-layout {
        text-align: center;
        margin: 0 auto;
        width: 327px;
      }
    }
    
    p {
      @include for-mobile-layout {
        text-align: center;
        margin: 0 auto;
        width: 327px;
      }
      opacity: 0.8;
      font-size: rem(15px);
      line-height: 25px;
    }
  }
}
