.main-info-container {
  background-color: $clr2;
  position: relative;
  
  .main-info {
    position: relative;
    max-width: 375px;
    margin: 0 auto;
    height: 1073px;
    padding: 100px 24px 0;
    @include for-mobile-layout {
      flex-direction: column;
    }
    
    @include for-wide-layout {
      max-width: 1440px;
      height: 684px;
      padding: 200px 165px 0;
    }
    .sep {
      width: 50px;
      height: 4px;
      background-color: $clr3;
      position: absolute;
      left: 165px;
      top: 140px;
      @include for-mobile-layout {
        left: 24px;
        top: 64px;
      }
    }

    h1 {
      width: 240px;
      font-family: livvic-bold;
      font-size: rem(32px);
      @include for-mobile-layout {
        line-height: 32px;
        margin-bottom: 55px;
      }
      
      @include for-wide-layout {
        width: 445px;
        font-size: 48px;
        margin-inline-end: 130px;
      }
    }

    .skill-list {
      width: 540px;
      display: flex;
      flex-direction: column;
      gap: 35px;
    }
  }
  .img-container {
    position: absolute;
    bottom: 0;
    right: 0;
    @include for-mobile-layout {
      top: 0;
    }
  }
}
